.hourly-container {
  max-width: 90%;
  text-align: center;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* flex-wrap: wrap; */
  /* height: 100%; */
  font-weight: 200;
}

.hourly-container > h2 {
  font-size: 2.5rem;
  font-weight: 300;
}

