.hourly-container__list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  transform: scale(0.9);
  padding: 1rem;

  /* flex-wrap: wrap; */
}

.hour__listitem {
  padding: 0.1rem;
